        <button v-for="(item,index) in datos.menus" :key="index" class="btn-secondary" v-on:click="mostrar(item,index)">{{item}}</button>
    </div>
    <body class="jumbotron">
       <span v-html="datos.content"></span>
    </body>
  </div>
</template>

<script>

export default {
  name: "Home",
  data() {
    return {
      
      datos:{
        "cabecera": "",
        "menus": ["Qué é isto?","Para quén está deseñado","Documentación"],
        "saludos": ["Hola","Ola","Bojour","Hello","Gütten","Ni Hao"],
        "content": "",
      },
      
    };
  },
  mounted() {
        var dado = Math.floor(Math.random() * 6);
        this.datos.cabecera = this.datos.saludos[dado];
  },
  methods: {
    mostrar(item,index){
      this.datos.cabecera = item;
      switch (index) {
        case 0:
          
          break;
        case 2:
          this.datos.content = "<a href=\"docs/TFG_DOCUMENTACION.pdf\" target=\"_blank\">pdf</a>";
          break;
        default:
          this.datos.content = "<h3>HOLA</h3>"
          break;
      }
    }
  },
};
</script>